	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Vacation Weekend","date":"18 Aug 2002","slug":"/2002-08-18-vacation-weekend/","tags":"Y2002"},"html":"<p>We're in vacation mode this weekend. Yesterday we took Christopher and his grandparents to a <a href=\"http://www.wingst.de/spiel_und_sportpark/home.htm\">play and sport  park</a> at Wingst, drove on to Cuxhaven to spend an hour on the beach, and had dinner at the <a href=\"http://www.schaufenster-fischereihafen.de/\">renovated fishery harbor</a>  at Bremerhaven. Today we're staying the backyard.</p>"},"next":{"fields":{"title":"Perfect World","date":"17 Aug 2002","slug":"/2002-08-17-perfect-world/"}},"previous":{"fields":{"title":"Flood Water Coming","date":"19 Aug 2002","slug":"/2002-08-19-flood-water-coming/"}}},{"node":{"fields":{"title":"Perfect World","date":"17 Aug 2002","slug":"/2002-08-17-perfect-world/","tags":"Y2002"},"html":"<p>Some moments the world seems perfect. Like last evening. When the weather is good, Hamburg is one of the nicest cities in the world. Of course, the weather is good only 10 days per year. But last night the weather was perfect. 25&deg;C. sunny. slight breeze. We took Christopher and his grandparents to the <a href=\"/2000/07/23/1313.php\">Hamburger Dom</a>, where he rode the kiddie train at least 10 times. And driving home, the rush hour traffic had dispersed, the sun-roof was open, the radio was blasting, and I thought to myself that the moment was perfect. And it was.</p>"},"next":{"fields":{"title":"Lost In The Flood","date":"16 Aug 2002","slug":"/2002-08-16-lost-in-the-flood/"}},"previous":{"fields":{"title":"Vacation Weekend","date":"18 Aug 2002","slug":"/2002-08-18-vacation-weekend/"}}},{"node":{"fields":{"title":"Lost In The Flood","date":"16 Aug 2002","slug":"/2002-08-16-lost-in-the-flood/","tags":"Y2002"},"html":"<p>We interrupt the election campaign for a natural disaster. <a href=\"http://www.cnn.com/2002/WORLD/europe/08/15/europe.floods/index.html\">CNN.com - Dresden evacuated as waters rise</a>. Over 100,000 have been evacuated along the Elbe and Mulde in Saxony, and damage has been estimated at several billion Euros. The investment made in rebuilding eastern Germany the past 12 years is being literally washed down the drain. The Elbe, of course, flows through Hamburg on its way to the North Sea. I haven't heard any estimates of what disruption or damage the waters could cause up here.</p>"},"next":{"fields":{"title":"WAMPP, LAMPP, Thank You MAMMP","date":"15 Aug 2002","slug":"/2002-08-15-wampp-lampp-thank-you-mammp/"}},"previous":{"fields":{"title":"Perfect World","date":"17 Aug 2002","slug":"/2002-08-17-perfect-world/"}}}],"pathPrefix":"","first":false,"last":false,"index":858,"pageCount":1101}}

